<template>
    <div class="home">
        {{msg}}
    </div>
</template>

<script>
export default {
    name:'home',
    data () {
        return {
            msg:''
        }
    },
    created(){
        this.Message(this)
    },
    methods:{
        Message(self){
            setTimeout(() => {
                self.msg = '打卡页'
            }, 2000);
        }
    }
}
</script>

<style lang="less" scoped>

</style>



