import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'//首页
import Clock from '@/pages/Clock'//打卡页
import User from '@/pages/User'//个人中心页

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/clock',
      name: 'clock',
      component: Clock
    },
    {
      path: '/user',
      name: 'user',
      component: User
    }
  ],
  // mode:'history',
  base:'/TinydreamWeb/mvvm/dream/dist'
})



